



















import {
  defineComponent,
  ref,
  watchEffect
} from '@vue/composition-api'

import useBoundingClientRect from '@/utils/use-bounding-client-rect'

export default defineComponent({
  setup (_, { emit }) {
    const el = ref()
    const svgSize = useBoundingClientRect(el)

    watchEffect(() => {
      emit('resized', svgSize.value)
    })

    return {
      el,
      svgSize
    }
  }
})
